import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Facebook } from '@ionic-native/facebook';
import { UsuariosModel } from '../../models/UsuariosModel';

/**
 * Generated class for the LoginFacebookPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login-facebook',
  templateUrl: 'login-facebook.html',
})
export class LoginFacebookPage {
 
  //Variavel para armazenar usuario
  userData = null;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public facebook: Facebook
    ) {
  }

  //m√©todo para chamar api do facebook e salvar no banco o usuario    
  loginFacebook() {
    let permissions = new Array<string>();
    permissions = ["public_profile", "email"];

    this.facebook.login(permissions).then((response) => {
    let params = new Array<string>();

    this.facebook.api("/me?fields=name,email", params)
    .then(res => {

        //estou usando o model para criar os usuarios
        let usuario = new UsuariosModel();
        usuario.nome = res.name;
        usuario.email = res.email;
        usuario.senha = res.id;
        usuario.login = res.email;
      
        this.logar(usuario);

    }, (error) => {
      alert(error);
      console.log('ERRO LOGIN: ',error);
    })
  }, (error) => {
    alert(error);
  });
  }

  logar(usuario: UsuariosModel) {
    //this.salvarService.salvarFacebook(usuario)
    //.then(() => {
        console.log('Usuario cadastrado via facebook com sucesso!');
    //})
  }
}




